.skill-container{
    height: 42vw;
    background-color: black;
}
.skill-intro{
    font-size: 5vw;
    background: -webkit-linear-gradient(
        90deg,
        rgb(12, 0, 241) 0%,
        rgb(18, 214, 165) 21%,
        rgb(44, 185, 178) 51%,
        rgb(8, 255, 201) 100%
      );
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bolder;
    text-align: center;
    padding-bottom: 3vw;
}
.slide-intro{
    height: 33vw;
    padding-left: 15vw;
    width: 50%;
    display: table-cell;
    float: left;
}
.slide-intro-right{
    width: 50%;
    display: table-cell;
    padding-top: 5vw;
    padding-left: 5vw;
    color: white;
}
.slide-intro-right span{
    font-size: 1vw;
    color: white;
    padding: 0.7vw;
    /* margin-right: 2vw; */
}
.slide-heading{
    color: rgb(255, 255, 255);
    text-align: center;
    padding-left: 35%;
    padding-bottom: 2vw;
    font-weight: bolder;
    font-size: 3vw;
}
.slide-heading span{
    color: rgb(255, 255, 255);
}

#UI{
    color: rgb(150, 120, 40);
}
#BT{
    color: rgb(51, 75, 184);
}
#DB{
    color: rgb(19, 141, 94);
}
#AD{
    color: rgb(55, 204, 204);
}